const days = ['segunda', 'ter√ßa', 'quarta', 'quinta', 'sexta', 's√°bado'];
const dayLabels = ['Segunda', 'Ter√ßa', 'Quarta', 'Quinta', 'Sexta', 'S√°bado'];

export default function MainApp() {
  const [currentUser, setCurrentUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [showDevPanel, setShowDevPanel] = useState(false);
  
  // App states
  const [selectedDay, setSelectedDay] = useState('segunda');
  const [showCalendar, setShowCalendar] = useState(false);
  const [showWorkoutEditor, setShowWorkoutEditor] = useState(false);
  const [showStats, setShowStats] = useState(false);
  const [showAttendance, setShowAttendance] = useState(false);
  const [showProfile, setShowProfile] = useState(false);
  const [workoutData, setWorkoutData] = useState(trainingData);
  const [attendanceCount, setAttendanceCount] = useState(0);
  const [weeklyProgress, setWeeklyProgress] = useState(75);

  const currentTraining = workoutData[selectedDay];

  // Check for existing session on mount
  useEffect(() => {
    const savedUser = localStorage.getItem('gym_current_user');
    if (savedUser) {
      setCurrentUser(JSON.parse(savedUser));
    }
    setIsLoading(false);
  }, []);

  // Load attendance count and calculate progress
  useEffect(() => {
    if (currentUser) {
      const attendanceHistory = JSON.parse(localStorage.getItem('attendanceHistory') || '[]');
      setAttendanceCount(attendanceHistory.length);
      
      // Calculate weekly progress
      const weekAgo = new Date();
      weekAgo.setDate(weekAgo.getDate() - 7);
      
      const thisWeekWorkouts = attendanceHistory.filter((record: any) => {
        const recordDate = new Date(record.date);
        return recordDate > weekAgo;
      });
      
      const weeklyGoal = 4; // 4 treinos por semana
      const progress = Math.min((thisWeekWorkouts.length / weeklyGoal) * 100, 100);
      setWeeklyProgress(Math.round(progress));
    }
  }, [attendanceCount, currentUser]);

  // Dev panel shortcut (Ctrl+Shift+D)
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if (e.ctrlKey && e.shiftKey && e.key === 'D') {
        e.preventDefault();
        setShowDevPanel(true);
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, []);

  const handleLogin = (user: User) => {
    setCurrentUser(user);
  };

  const handleLogout = () => {
    localStorage.removeItem('gym_current_user');
    setCurrentUser(null);
  };

  const handleSaveWorkout = (exercises: Exercise[]) => {
    setWorkoutData({
      ...workoutData,
      [selectedDay]: {
        ...currentTraining,
        exercises: exercises
      }
    });
  };

  const handleScheduleWorkout = () => {
    setShowCalendar(true);
  };

  const handleDailyChallenge = () => {
    const challenges = [
      "Fa√ßa 50 flex√µes hoje!",
      "Caminhe 10.000 passos",
      "Beba 2 litros de √°gua",
      "Fa√ßa 3 s√©ries de prancha de 30s",
      "Suba escadas por 10 minutos"
    ];
    const randomChallenge = challenges[Math.floor(Math.random() * challenges.length)];
    alert(`üèÜ Desafio Di√°rio:\n${randomChallenge}`);
  };

  const handleAttendanceRegistered = () => {
    const attendanceHistory = JSON.parse(localStorage.getItem('attendanceHistory') || '[]');
    setAttendanceCount(attendanceHistory.length);
  };

  if (isLoading) {
    return (
      <div className="min-h-screen bg-slate-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-16 h-16 border-4 border-blue-600 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-gray-600">Carregando MyTrainer...</p>
        </div>
      </div>
    );
  }

  if (!currentUser) {
    return <AuthScreen onLogin={handleLogin} />;
  }

  return (
    <div className="min-h-screen bg-slate-50">
      <div className="container mx-auto px-4 py-8 max-w-4xl pb-24">
        {/* Header with logout */}
        <div className="flex justify-between items-center mb-8">
          <div className="flex items-center">
            <div className="w-12 h-12 rounded-full bg-blue-600 flex items-center justify-center mr-3">
              <span className="text-white font-bold text-lg">MT</span>
            </div>
            <div>
              <h1 className="text-2xl font-bold text-gray-800">MyTrainer</h1>
              <p className="text-sm text-gray-600">Ol√°, {currentUser.name}!</p>
            </div>
          </div>
          
          <div className="flex items-center space-x-4">
            <button 
              onClick={() => setShowDevPanel(true)}
              className="p-2 rounded-full bg-gray-100 text-gray-600 hover:bg-gray-200 transition-colors text-sm"
              title="Painel do Desenvolvedor (Ctrl+Shift+D)"
            >
              üîß
            </button>
            <button 
              onClick={handleLogout}
              className="p-2 rounded-full bg-red-100 text-red-600 hover:bg-red-200 transition-colors"
              title="Sair"
            >
              üö™
            </button>
          </div>
        </div>
        
        {/* Welcome and Summary */}
        <div className="bg-white rounded-xl p-6 mb-8 shadow-sm">
          <div className="flex flex-col md:flex-row md:items-center md:justify-between">
            <div>
              <h2 className="text-xl font-semibold text-gray-800">
                Bem-vindo, <span className="text-blue-600">{currentUser.name.split(' ')[0]}</span>!
              </h2>
              <p className="text-gray-600">Veja seus treinos e acompanhe seu progresso.</p>
            </div>
            <div className="mt-4 md:mt-0">
              <div className="flex items-center">
                <div className="relative w-16 h-16 mr-4">
                  <div className="w-full h-full rounded-full bg-gray-200">
                    <div className="w-full h-full rounded-full bg-green-500 transform -rotate-90 origin-center" 
                         style={{ clipPath: 'polygon(50% 50%, 50% 0%, 87.5% 12.5%, 50% 50%)' }}>
                    </div>
                  </div>
                  <div className="absolute inset-0 flex items-center justify-center">
                    <span className="text-xs font-bold">{weeklyProgress}%</span>
                  </div>
                </div>
                <div>
                  <p className="text-sm text-gray-600">Progresso semanal</p>
                  <p className="font-semibold">{Math.floor(weeklyProgress * 4 / 100)}/4 treinos completos</p>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Training Days Navigation */}
        <div className="flex overflow-x-auto pb-2 mb-6 gap-2">
          {days.map((day, index) => (
            <button
              key={day}
              onClick={() => setSelectedDay(day)}
              className={`flex-shrink-0 px-4 py-2 rounded-lg font-medium transition-colors ${
                selectedDay === day
                  ? 'bg-blue-600 text-white'
                  : 'bg-white border border-gray-200 text-gray-700 hover:bg-gray-50'
              }`}
            >
              {dayLabels[index]}
            </button>
          ))}
        </div>

        {/* Current Training Section */}
        <div className="mb-8">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-xl font-semibold text-gray-800">
              Treino de {currentTraining.title}
            </h2>
            <div className="flex items-center gap-2">
              <button
                onClick={() => setShowWorkoutEditor(true)}
                className="px-3 py-1 bg-green-100 text-green-600 rounded-full text-sm font-medium hover:bg-green-200 transition-colors"
              >
                Ajustar Treino
              </button>
              <span className="px-3 py-1 bg-blue-100 text-blue-600 rounded-full text-sm font-medium">
                {currentTraining.duration}
              </span>
            </div>
          </div>

          {/* Exercises List */}
          <div className="space-y-4">
            {currentTraining.exercises.map((exercise) => (
              <ExerciseCard key={exercise.id} exercise={exercise} />
            ))}
          </div>
        </div>

        {/* Quick Actions */}
        <div className="grid grid-cols-3 gap-3">
          <button 
            onClick={() => setShowAttendance(true)}
            className="bg-white p-4 rounded-xl shadow-sm flex items-center justify-center flex-col border border-gray-100 hover:border-green-200 transition-colors"
          >
            <div className="w-12 h-12 rounded-full bg-green-100 text-green-600 flex items-center justify-center mb-2">
              <span className="text-xl font-bold">‚úì</span>
            </div>
            <span className="text-sm font-medium">Registrar Frequ√™ncia</span>
          </button>
          <button 
            onClick={handleScheduleWorkout}
            className="bg-white p-4 rounded-xl shadow-sm flex items-center justify-center flex-col border border-gray-100 hover:border-blue-200 transition-colors"
          >
            <div className="w-12 h-12 rounded-full bg-blue-100 text-blue-600 flex items-center justify-center mb-2">
              <span className="text-xl font-bold">+</span>
            </div>
            <span className="text-sm font-medium">Agendar Treino</span>
          </button>
          <button 
            onClick={handleDailyChallenge}
            className="bg-white p-4 rounded-xl shadow-sm flex items-center justify-center flex-col border border-gray-100 hover:border-purple-200 transition-colors"
          >
            <div className="w-12 h-12 rounded-full bg-purple-100 text-purple-600 flex items-center justify-center mb-2">
              <span className="text-xl font-bold">‚òÖ</span>
            </div>
            <span className="text-sm font-medium">Desafio Di√°rio</span>
          </button>
        </div>
      </div>

      <BottomNav 
        onCalendarClick={() => setShowCalendar(true)} 
        onStatsClick={() => setShowStats(true)}
        onProfileClick={() => setShowProfile(true)}
      />
      
      {/* Modals */}
      {showCalendar && (
        <CalendarModal onClose={() => setShowCalendar(false)} />
      )}
      
      {showWorkoutEditor && (
        <WorkoutEditor
          isOpen={showWorkoutEditor}
          onClose={() => setShowWorkoutEditor(false)}
          currentExercises={currentTraining.exercises}
          onSave={handleSaveWorkout}
          workoutTitle={currentTraining.title}
        />
      )}
      
      {showStats && (
        <StatsModal
          isOpen={showStats}
          onClose={() => setShowStats(false)}
        />
      )}
      
      {showAttendance && (
        <AttendanceTracker
          isOpen={showAttendance}
          onClose={() => setShowAttendance(false)}
          onAttendanceRegistered={handleAttendanceRegistered}
        />
      )}
      
      {showProfile && (
        <ProfileModal
          isOpen={showProfile}
          onClose={() => setShowProfile(false)}
        />
      )}

      {showDevPanel && (
        <DevPanel
          isOpen={showDevPanel}
          onClose={() => setShowDevPanel(false)}
        />
      )}
    </div>
  );
}
