"use client";

import { useState, useEffect } from 'react';

interface User {
  id: string;
  email: string;
  password: string;
  name: string;
  createdAt: string;
}

interface DevPanelProps {
  isOpen: boolean;
  onClose: () => void;
}

export default function DevPanel({ isOpen, onClose }: DevPanelProps) {
  const [users, setUsers] = useState<User[]>([]);
  const [attendanceData, setAttendanceData] = useState<any[]>([]);
  const [workoutData, setWorkoutData] = useState<any[]>([]);
  const [activeTab, setActiveTab] = useState('users');

  useEffect(() => {
    if (isOpen) {
      loadData();
      document.body.style.overflow = 'hidden';
    }
    return () => {
      document.body.style.overflow = '';
    };
  }, [isOpen]);

  const loadData = () => {
    const usersData = JSON.parse(localStorage.getItem('gym_users') || '[]');
    const attendanceHistory = JSON.parse(localStorage.getItem('attendanceHistory') || '[]');
    const workoutHistory = JSON.parse(localStorage.getItem('workoutHistory') || '[]');
    
    setUsers(usersData);
    setAttendanceData(attendanceHistory);
    setWorkoutData(workoutHistory);
  };

  const deleteUser = (userId: string) => {
    if (confirm('Tem certeza que deseja deletar este usu√°rio?')) {
      const updatedUsers = users.filter(user => user.id !== userId);
      setUsers(updatedUsers);
      localStorage.setItem('gym_users', JSON.stringify(updatedUsers));
    }
  };

  const clearAllData = () => {
    if (confirm('‚ö†Ô∏è ATEN√á√ÉO: Isso ir√° apagar TODOS os dados do app. Tem certeza?')) {
      localStorage.removeItem('gym_users');
      localStorage.removeItem('gym_current_user');
      localStorage.removeItem('attendanceHistory');
      localStorage.removeItem('workoutHistory');
      localStorage.removeItem('userProfile');
      loadData();
      alert('Todos os dados foram apagados!');
    }
  };

  const createDemoData = () => {
    const demoUsers: User[] = [
      {
        id: '1',
        email: 'admin@mytrainer.com',
        password: '123456',
        name: 'Administrador',
        createdAt: new Date().toISOString()
      },
      {
        id: '2',
        email: 'joao@email.com',
        password: '123456',
        name: 'Jo√£o Silva',
        createdAt: new Date().toISOString()
      },
      {
        id: '3',
        email: 'maria@email.com',
        password: '123456',
        name: 'Maria Santos',
        createdAt: new Date().toISOString()
      }
    ];

    const demoAttendance = [
      {
        id: Date.now().toString(),
        date: new Date().toISOString(),
        workoutType: 'Costas e B√≠ceps',
        duration: 60,
        exercises: [],
        notes: 'Treino demo'
      }
    ];

    localStorage.setItem('gym_users', JSON.stringify(demoUsers));
    localStorage.setItem('attendanceHistory', JSON.stringify(demoAttendance));
    loadData();
    alert('Dados demo criados com sucesso!');
  };

  const exportData = () => {
    const allData = {
      users: users,
      attendance: attendanceData,
      workouts: workoutData,
      exportDate: new Date().toISOString()
    };
    
    const dataStr = JSON.stringify(allData, null, 2);
    const dataBlob = new Blob([dataStr], { type: 'application/json' });
    const url = URL.createObjectURL(dataBlob);
    
    const link = document.createElement('a');
    link.href = url;
    link.download = `mytrainer-backup-${new Date().toISOString().split('T')[0]}.json`;
    link.click();
    
    URL.revokeObjectURL(url);
  };

  if (!isOpen) return null;

  const handleBackdropClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  return (
    <div 
      className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4"
      onClick={handleBackdropClick}
    >
      <div className="bg-white rounded-lg shadow-xl max-w-4xl w-full max-h-[90vh] overflow-auto">
        <div className="p-6">
          {/* Header */}
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-2xl font-semibold text-gray-800 flex items-center">
              <span className="text-2xl mr-2">üîß</span>
              Painel do Desenvolvedor
            </h2>
            <button 
              onClick={onClose}
              className="text-gray-600 hover:text-gray-800 text-2xl font-bold w-8 h-8 flex items-center justify-center rounded-full hover:bg-gray-100 transition-colors"
            >
              √ó
            </button>
          </div>

          {/* Tabs */}
          <div className="flex mb-6 border-b">
            <button
              onClick={() => setActiveTab('users')}
              className={`px-4 py-2 font-medium ${
                activeTab === 'users' 
                  ? 'text-blue-600 border-b-2 border-blue-600' 
                  : 'text-gray-600 hover:text-gray-800'
              }`}
            >
              Usu√°rios ({users.length})
            </button>
            <button
              onClick={() => setActiveTab('data')}
              className={`px-4 py-2 font-medium ${
                activeTab === 'data' 
                  ? 'text-blue-600 border-b-2 border-blue-600' 
                  : 'text-gray-600 hover:text-gray-800'
              }`}
            >
              Dados ({attendanceData.length + workoutData.length})
            </button>
            <button
              onClick={() => setActiveTab('actions')}
              className={`px-4 py-2 font-medium ${
                activeTab === 'actions' 
                  ? 'text-blue-600 border-b-2 border-blue-600' 
                  : 'text-gray-600 hover:text-gray-800'
              }`}
            >
              A√ß√µes
            </button>
          </div>

          {/* Users Tab */}
          {activeTab === 'users' && (
            <div className="space-y-4">
              <h3 className="text-lg font-medium text-gray-800">Usu√°rios Cadastrados</h3>
              {users.length === 0 ? (
                <p className="text-gray-500 text-center py-8">Nenhum usu√°rio cadastrado</p>
              ) : (
                <div className="space-y-3">
                  {users.map((user) => (
                    <div key={user.id} className="bg-gray-50 rounded-lg p-4 flex justify-between items-center">
                      <div>
                        <h4 className="font-medium text-gray-800">{user.name}</h4>
                        <p className="text-sm text-gray-600">{user.email}</p>
                        <p className="text-xs text-gray-500">
                          Criado em: {new Date(user.createdAt).toLocaleDateString('pt-BR')}
                        </p>
                      </div>
                      <div className="flex items-center gap-2">
                        <span className="text-xs bg-gray-200 px-2 py-1 rounded">
                          ID: {user.id}
                        </span>
                        <button
                          onClick={() => deleteUser(user.id)}
                          className="text-red-600 hover:text-red-800 text-sm"
                        >
                          üóëÔ∏è Deletar
                        </button>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          )}

          {/* Data Tab */}
          {activeTab === 'data' && (
            <div className="space-y-6">
              <div>
                <h3 className="text-lg font-medium text-gray-800 mb-3">Registros de Frequ√™ncia</h3>
                {attendanceData.length === 0 ? (
                  <p className="text-gray-500">Nenhum registro de frequ√™ncia</p>
                ) : (
                  <div className="bg-gray-50 rounded-lg p-4 max-h-40 overflow-y-auto">
                    {attendanceData.map((record, index) => (
                      <div key={index} className="text-sm mb-2">
                        <span className="font-medium">{record.workoutType}</span> - 
                        <span className="text-gray-600"> {record.duration}min</span> - 
                        <span className="text-gray-500"> {new Date(record.date).toLocaleDateString('pt-BR')}</span>
                      </div>
                    ))}
                  </div>
                )}
              </div>

              <div>
                <h3 className="text-lg font-medium text-gray-800 mb-3">Registros de Exerc√≠cios</h3>
                {workoutData.length === 0 ? (
                  <p className="text-gray-500">Nenhum registro de exerc√≠cio</p>
                ) : (
                  <div className="bg-gray-50 rounded-lg p-4 max-h-40 overflow-y-auto">
                    {workoutData.map((record, index) => (
                      <div key={index} className="text-sm mb-2">
                        <span className="font-medium">{record.exerciseName}</span> - 
                        <span className="text-gray-600"> {record.load}kg</span> - 
                        <span className="text-gray-500"> {new Date(record.date).toLocaleDateString('pt-BR')}</span>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            </div>
          )}

          {/* Actions Tab */}
          {activeTab === 'actions' && (
            <div className="space-y-4">
              <h3 className="text-lg font-medium text-gray-800">A√ß√µes do Desenvolvedor</h3>
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <button
                  onClick={createDemoData}
                  className="p-4 bg-green-50 border border-green-200 rounded-lg hover:bg-green-100 transition-colors text-left"
                >
                  <h4 className="font-medium text-green-800">üéØ Criar Dados Demo</h4>
                  <p className="text-sm text-green-600 mt-1">
                    Cria usu√°rios e dados de exemplo para teste
                  </p>
                </button>

                <button
                  onClick={exportData}
                  className="p-4 bg-blue-50 border border-blue-200 rounded-lg hover:bg-blue-100 transition-colors text-left"
                >
                  <h4 className="font-medium text-blue-800">üíæ Exportar Dados</h4>
                  <p className="text-sm text-blue-600 mt-1">
                    Baixa backup de todos os dados em JSON
                  </p>
                </button>

                <button
                  onClick={loadData}
                  className="p-4 bg-yellow-50 border border-yellow-200 rounded-lg hover:bg-yellow-100 transition-colors text-left"
                >
                  <h4 className="font-medium text-yellow-800">üîÑ Recarregar Dados</h4>
                  <p className="text-sm text-yellow-600 mt-1">
                    Atualiza os dados exibidos no painel
                  </p>
                </button>

                <button
                  onClick={clearAllData}
                  className="p-4 bg-red-50 border border-red-200 rounded-lg hover:bg-red-100 transition-colors text-left"
                >
                  <h4 className="font-medium text-red-800">üóëÔ∏è Limpar Tudo</h4>
                  <p className="text-sm text-red-600 mt-1">
                    ‚ö†Ô∏è Remove todos os dados permanentemente
                  </p>
                </button>
              </div>

              {/* Database Info */}
              <div className="bg-gray-50 rounded-lg p-4 mt-6">
                <h4 className="font-medium text-gray-800 mb-2">üìä Informa√ß√µes do Banco</h4>
                <div className="text-sm text-gray-600 space-y-1">
                  <p>‚Ä¢ Tipo: LocalStorage (Desenvolvimento)</p>
                  <p>‚Ä¢ Usu√°rios: {users.length}</p>
                  <p>‚Ä¢ Registros de Frequ√™ncia: {attendanceData.length}</p>
                  <p>‚Ä¢ Registros de Exerc√≠cios: {workoutData.length}</p>
                  <p>‚Ä¢ √öltima atualiza√ß√£o: {new Date().toLocaleString('pt-BR')}</p>
                </div>
              </div>
            </div>
          )}

          {/* Close Button */}
          <div className="mt-6">
            <button 
              onClick={onClose}
              className="w-full px-4 py-2 bg-gray-600 text-white rounded-lg hover:bg-gray-700 transition-colors"
            >
              Fechar Painel
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
